.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.directional, .time {
  width: 8vmin;
}

.wrapper {
  position: relative;
  width: 60vmin;
  height: 60vmin;
  justify-self: right;
}

main {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.sideboard {
  justify-self: left;
  
}


